@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Orbitron:wght@400;700;900&display=swap");

body {
    background: url("../public/images/mainbg.jpeg");
    background-size: cover;
    overflow-x: hidden;
}

.active {
    background: linear-gradient(45deg, #cc7fc8 20%, #922b8d 70%);
    border-radius: 50%;
    color: #fff;
}

.text-gradient {
    @apply bg-gradient-to-r text-transparent bg-clip-text from-[#922b8d] to-[#cc7fc8] font-bold;
}

.bg-gradient {
    background: linear-gradient(
        92.23deg,
        #406aff 21.43%,
        #3bace2 50.63%,
        #b936ee 100%,
        #ff56f6 117.04%
    );
}
.section {
    @apply py-8 lg:py-24 lg:h-screen flex items-center;
}
